import Home from "./views/home"
import Test from "./views/test"
import ClaimPage from "./views/Claims/ClaimPage"
import Profile from "./views/profile"
import Login from "./views/Login/Login"
import Footer from "./nav/Footer/Footer"
import Header from "./nav/Header/Header"
import Navbar from "./views/Home/Navbar"
import NewError from "./views/ErrorsViews/NewError"
import ErrorManager from "./views/ErrorsViews/ErrorManager"

export const routes = {
	LOGIN: "/",
	HOME: "/home",
	PROFILE: "/profile",
	CLAIMPAGE: "/ClaimPage",
	TEST: "/test",
	ERRORMANAGER: "/ErrorManager",
	NEWERROR: "/NewError"
}

export const apiroutes = {
	BASEROUTE: "https://localhost:7777"
	
}



export const routeserver = [
	{
		path: "/Claims",
		component: ClaimPage,
	},	
	{
		path: "/home",
		component: Home,
	},
	{
		path: "/profile",
		component: Profile,
	},
	{
		path: "/ErrorManager",
		component: ErrorManager,
	},
	{
		path: "/NewError",
		component: NewError,
	},
]